@mixin fw-sm {
    font-weight: $fw-sm;
}
@mixin fw-rg {
    font-weight: $fw-rg;
}
@mixin fw-md {
    font-weight: $fw-md;
}
@mixin fw-bd {
    font-weight: $fw-bg;
}
@mixin fw-bk {
    font-weight: $fw-bk;
}
//RWD
$nb-media: 1280px;
$pad-media: 992px;
$phone-media: 767px;

//
@mixin nb-width() {
    @media all and (max-width: $nb-media) {
        @content;
    }
}

//
@mixin pad-width() {
    @media all and (max-width: $pad-media) {
        @content;
    }
}

//
@mixin phone-width() {
    @media all and (max-width: $phone-media) {
        @content;
    }
}
